@import "reset";
@import "fonts";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  font-family: $font-primary;
  .icon {
    transition: all .2s ease;
  }
  a {
    transition: all 0.2s ease;
    color: inherit;
    &:hover {
      text-decoration: none;
    }
  }
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.bold-text {
  font-weight: bold;
}

.account__avatar {
  width: 37px;
  height: 37px;
  margin-right: 13px;
  border-radius: 50%;
  overflow: hidden;
  img {
    display: block;
    width: 100%;
  }
}

// HEADER
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 74px;
  padding-left: 30px;
  padding-right: 20px;
  background-color: #28292d;
  @media only screen and (max-width: 480px) {
    flex-direction: column;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.header__left {
  display: flex;
  align-items: baseline;
  padding-bottom: 5px;
  @media only screen and (max-width: 768px) {
    flex-direction: column;
  }
  @media only screen and (max-width: 480px) {
    margin-bottom: 15px;
  }
}

.header__logo {
  margin-right: 21px;
}

.header__logo-link {
  width: 121px;
  height: 28px;
}

.header__title {
  padding: 0;
  margin: 0;
}

.icon-logo {
  height: 28px;
  width: 121px;
}

.header__title {
  font-size: 13.1px;
  color: #888888;
}

.header__navbar {
  display: flex;
  align-items: center;
  color: #999999;
  @media only screen and (max-width: 768px) {
    padding-top: 15px;
    padding-bottom: 10px;
    flex-direction: column;
  }
}

.header__notification {
  display: flex;
  align-items: center;
  padding-top: 10px;
}

.navbar__item {
  display: flex;
  align-items: center;
}

.navbar__icon {
  position: relative;
  padding: 0;
  margin: 0;
}

.navbar__circle {
  position: absolute;
  text-align: center;
  align-items: center;
  display: inline-block;
  width: 16px;
  height: 16px;
  font-size: 9.7px;
  line-height: 18px;
  font-weight: bold;
  color: #fff;
  border-radius: 50%;
  background-color: #9caa49;
  transition: all .2s ease;
}

.navbar__circle--green {
  left: -8px;
  top: -10px;
  &:hover {
    background-color: lighten(#9caa49, 7%);
  }
}

.navbar__circle--red {
  left: -9px;
  top: -7px;
  background-color: #ff7e1b;
  &:hover {
    background-color: lighten(#ff7e1b, 7%);
  }
}

.icon-email {
  width: 20px;
  height: 15px;
}

.navbar__inbox {
  margin-right: 17px;
  .navbar__text {
    margin-left: 7px;
  }
}

.navbar__text {
  font-size: 13px;
  line-height: 1.2;
  letter-spacing: -0.3px;
  transition: color 0.2s ease;
  &:hover {
    color: #fff;
  }
}

.navbar__alert {
  margin-right: 26px;
  @media only screen and (max-width: 480px) {
    margin-right: 0;
  }
}

.icon-alert {
  width: 22px;
  height: 18px;
}

.navbar__account {
  display: flex;
  align-items: center;
  padding-left: 16px;
  border-left: 1px solid #444444;
  @media only screen and (max-width: 768px) {
    margin-top: 15px;
    border-left: none;
  }
  @media only screen and (max-width: 480px) {
    padding-left: 0;
  }
}

.account__hello {
  font-size: 16px;
  line-height: 1.2;
  text-align: left;
  color: #9caa49;
}

.account__link {
  display: flex;
  align-items: center;
  .navbar__text {
    margin-right: 6px;
    font-size: 11px;
    line-height: 1.5;
    font-weight: 500;
    letter-spacing: 0;
  }
}

.icon-settings {
  width: 13px;
  height: 14px;
}

.navbar__turn-on {
  margin-left: 22px;
}

.icon-turn-on {
  width: 21px;
  height: 24px;
}

// MAIN
.main {
  position: relative;
  display: flex;
  min-height: calc(100vh - 74px);
  background-color: #fafafa;
}

// PRIMARY MENU
.main__navbar {
  position: relative;
  display: flex;
  padding-top: 45px;
  padding-bottom: 45px;
  color: #ececec;
  background-color: #5c5f69;
  transition: all 0.4s ease;
  &:hover {
    .secondmenu {
      width: 170px;
      visibility: visible;
    }
    .secondmenu-bg {
      width: 170px;
    }
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 78px;
    background-color: #3a3c44;
    z-index: 1;
    @media only screen and (max-width: 768px) {
      width: 50px;
    }
  }
  .secondmenu-bg {
    // content: "";
    position: absolute;
    top: 0;
    left: 78px;
    width: 0;
    height: 100%;
    background-color: #5c5f69;
    z-index: 1;
    transition: all 0.4s ease;
    @media only screen and (max-width: 768px) {
      left: 50px;
    }
  }
}

.menu {
  z-index: 10;
}

.menu__item {
  padding-top: 2px;
  padding-bottom: 2px;
  position: relative;
  display: flex;
  transition: all 0.2s ease;
  &:hover {
    .menu__icon {
      border-color: #9caa49;
    }
    .secondmenu__title {
      color: #ebebeb;
      background-color: #9caa49;
    }
  }
}

.menu__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 78px;
  height: 44px;
  background-color: #3a3c44;
  border-left: 4px solid transparent;
  @media only screen and (max-width: 768px) {
    width: 50px;
  }
}

.icon-projects {
  width: 16px;
  height: 15px;
}

.icon-pencil {
  width: 20px;
  height: 19px;
}

.icon-reports {
  width: 17px;
  height: 18px;
}

.icon-agenda {
  width: 21px;
  height: 20px;
}

.icon-maps {
  width: 13px;
  height: 20px;
  fill: #dddddd;
}

.icon-schedule {
  width: 18px;
  height: 17px;
}

.secondmenu {
  position: absolute;
  top: 0;
  left: 78px;
  width: 0;
  overflow: hidden;
  padding-left: 0px;
  transition: all 0.2s linear;
  background-color: #5c5f69;
  transition: all 0.4s ease;
  @media only screen and (max-width: 768px) {
    left: 50px;
  }
  &:hover {
    height: 300px;
  }
  &:hover .secondmenu__list {
    opacity: 1;
    display: flex;
    flex-direction: column;
    z-index: 1000;
  }
}

.secondmenu__title {
  position: relative;
  display: flex;
  align-items: center;
  height: 44px;
  padding-left: 24px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.57;
  z-index: 1;
  .chevron {
    position: absolute;
    top: 12px;
    left: 10px;
    display: inline-block;
  }
}

.icon-chevron-right {
  width: 4px;
  height: 8px;
}

.secondmenu__list {
  position: absolute;
  top: 44px;
  left: 0;
  width: 100%;
  opacity: 0;
  transition: all 0.4s ease;
  z-index: 100;
}

.secondmenu__item {
  background-color: lighten(#5c5f69, 5%);
  &:first-child {
    padding-top: 10px;
  }
  &:last-child {
    padding-bottom: 10px;
  }
}

.secondmenu__link {
  display: block;
  padding: 5px 18px 5px 24px;
  font-size: 13px;
  white-space: nowrap;
  &:hover {
    color: #999933;
  }
}

// MAIN CONTENT
.main__content {
  width: 100%;
  margin-left: 16.5%;
  padding-bottom: 30px;
  @media only screen and (max-width: 1200px) {
    margin-left: 7%;
    padding-right: 2%;
  }
}

.content__title {
  margin-top: 98px;
  margin-bottom: 45px;
  @include rfs(22);
  font-weight: 500;
  color: #99aa46;
  @media only screen and (max-width: 768px) {
    margin-top: 60px;
    margin-bottom: 22px;
  }
  span {
    font-weight: 300;
    color: #999999;
  }
}

.content__head {
  margin-bottom: 85px;
  display: flex;
  align-items: center;
  font-size: 16px;
  @media only screen and (max-width: 768px) {
    margin-bottom: 30px;
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

.content__box {
  padding-right: 27px;
  border-right: 1px solid #dddddd;
  @media only screen and (max-width: 480px) {
    padding-right: 0;
    border: none;
  }
}

.content__hello {
  @media only screen and (max-width: 480px) {
    margin-top: 10px;
  }
}

.content__name {
  font-weight: 600;
  color: #9caa49;
}

.content__text {
  font-size: 16px;
  color: #5c5f69;
}

.weather {
  display: flex;
  align-items: center;
  padding-left: 27px;
  @media only screen and (max-width: 480px) {
    margin-top: 10px;
    padding-left: 50px;
  }
}

.weather__box {
  line-height: 1;
  letter-spacing: 0.3px;
}

.weather__date {
  font-size: 14.8px;
  color: #9baa48;
}

.weather__forecast {
  font-size: 16.3px;
  color: #aaaaaa;
}

.weather__icon {
  margin-left: 13px;
}

.icon-weather {
  width: 44px;
  height: 32px;
}

// content blocks
.content__blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1010px;
  max-width: 1010px;
  @media only screen and (max-width: 1366px) {
    width: 100%;
  }
}

.blocks__item {
  flex-basis: 32.2%;
  max-width: 330px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 189px;
  margin-bottom: 13px;
  padding: 5.7%;
  background-color: #f2f2f2;
  @media only screen and (max-width: 768px) {
    flex-basis: 49%;
    align-items: center;
    height: 130px;
  }
  @media only screen and (max-width: 480px) {
    flex-basis: 100%;
  }
}

.icon-projects-big {
  width: 43px;
  height: 40px;
}

.icon-agenda-big {
  width: 41px;
  height: 39px;
}

.icon-schedule-big {
  width: 40px;
  height: 38px;
}

.icon-pencil-big {
  width: 43px;
  height: 39px;
}

.icon-reports-big {
  width: 36px;
  height: 38px;
}

.icon-maps-big {
  width: 25px;
  height: 40px;
  fill: #5ec2ab;
}

.block__name {
  margin-top: 15px;
  @include rfs(20);
  font-weight: 300;
  color: #5c5f69;
  @media only screen and (min-width: 1200px) {
    white-space: nowrap;
  }
}

// FOOTER 
.content__footer {
  display: flex;
  align-items: baseline;
  margin-top: 5%;
  padding-bottom: 5px;
  @media only screen and (max-width: 480px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer__logo-link {
    display: inline-block;
    margin-right: 12px;
    img {
      display: block;
      width: 98px;
      height: 23px;
    }
  }
  .footer__title {
    font-size: 13.1px;
    color: #cfd0d6;
  }
}


/****************
    MAIN TABLE 
 ***************/

.main__table {
  flex-grow: 1;
  @media only screen and (max-width: 992px) {
    overflow-x: scroll;
  }
  .icon {
    fill: #999999;
    &:hover {
      fill: darken(#999999, 10%)
    }
  }
  .arrowlink-back {
    padding: 20px 0 5px 48px;
    background-color: #f2f2f2;
    @media only screen and (max-width: 992px) {
      padding-left: 2%;
    }
  }
  .tabs {
    padding-left: 48px;
    @media only screen and (max-width: 1200px) {
      padding-left: 1%;
    }
    @media only screen and (max-width: 992px) {
      padding-left: 2%;
    }
    .tabbed {
      background-color: #f2f2f2;
    }
    section {
      background-color: #fafafa;
    }
    [role="tablist"] {
      position: relative;
      padding: 0;
      padding-right: 9%;
      @media only screen and (max-width: 1366px) {
        padding-right: 1%;
      }
      &:before {
        position: absolute;
        content: "";
        top: 0;
        left: -48px;
        display: block;
        height: 100%;
        width: 48px;
        background-color: #f2f2f2;
      }
    }
    [role="tablist"] li,
    [role="tablist"] a {
      display: inline-block;
    }
    [role="tablist"] a {
      text-decoration: none;
      padding: 12px 18px 14px 18px;
    }
    [role="tablist"] [aria-selected] {
      background: #fafafa;
      position: relative;
      top: 2px;
    }
    // [role="tabpanel"] *+* {
    //   margin-top: 0.75rem;
    // }
    *:focus {
      outline: none;
    }
    @media (max-width: 768px) {
      [role="tablist"] li,
      [role="tablist"] a {
        display: block;
        position: static;
      }
      [role="tablist"] a {
        border: 2px solid #ddd !important;
      }
      [role="tablist"] li+li a {
        border-top: 0 !important;
      }
      [role="tablist"] [aria-selected] {
        position: static;
      }
      [role="tablist"] [aria-selected]::after {
        content: '\0020⬅';
      }
      [role="tabpanel"] {
        border-top: 0;
      }
    }
  }
  .tabs__link {
    font-size: 13px;
    font-weight: 500;
    text-align: center;
    color: #999999;
  }
  .table__title {
    display: inline-block;
    margin-right: 30px;
    font-size: 15px;
    font-weight: 500;
    color: #5c5f69;
    @media only screen and (max-width: 768px) {
      margin-bottom: 10px;
    }
  }
  .table__search {
    float: right;
    padding-left: 40px;
    max-width: 229px;
    @media only screen and (max-width: 1200px) {
      float: none;
      display: inline-block;
    }
    @media only screen and (max-width: 992px) {
      display: block;
      padding-left: 0;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    @media only screen and (max-width: 480px) {
      max-width: 100%;
    }
  }
  .search {
    display: flex;
    align-items: center;
    input,
    button {
      border: none;
    }
    input {
      padding-left: 31px;
      height: 28px;
      color: #999999;
      background-color: #fafafa;
      @media only screen and (max-width: 480px) {
        width: 100%;
      }
    }
    input::placeholder {
      font-family: $font-primary;
      font-size: 12px;
      color: #999999;
      font-weight: 600;
    }
    .search__submit {
      margin-right: -28px;
      padding-top: 3px;
      font-size: 15px;
      background-color: transparent;
      z-index: 5;
      &:hover {
        cursor: pointer;
      }
    }
  }
  // progress bar
  .table__progress {
    display: flex;
    align-items: center;
    padding-top: 22px;
    padding-bottom: 36px;
    background-color: #fafafa;
    @media only screen and (max-width: 992px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  .progress {
    display: flex;
    align-items: center;
    height: 29px;
    padding-left: 13px;
    padding-right: 18px;
    color: #aaaaaa;
    border: 1px solid #ddd;
    overflow: hidden;
    @media only screen and (max-width: 768px) {
      padding-left: 2%;
      padding-right: 2%;
    }
    @media only screen and (max-width: 480px) {
      height: auto;
      flex-wrap: wrap;
      .icon {
        font-size: 16px;
      }
    }
  }
  .progress__step {
    display: flex;
    align-items: center;
    .icon {
      font-size: 30px;
    }
  }
  .progress__step--active {
    .progress__step-name {
      color: #71b6cc;
    }
    .icon {
      fill: #71b6cc;
      ;
    }
  }
  .progress__step-name {
    padding-left: 15px;
    padding-right: 10px;
    font-size: 12px;
    font-weight: 600;
    text-align: center;
    color: #aaaaaa;
    @media only screen and (max-width: 768px) {
      padding-left: 5px;
      padding-right: 3px;
    }
  }
  .progress__step-icon {
    padding-top: 3px;
  }
  // colored
  .colored {
    margin-left: 26px;
    display: flex;
    align-self: flex-end;
    @media only screen and (max-width: 992px) {
      margin-left: 0;
      margin-top: 20px;
      align-self: flex-start;
    }
    @media only screen and (max-width: 992px) {
      align-self: center;
    }
  }
  .colored__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 6px;
  }
  .colored__line {
    width: 51px;
    height: 4px;
  }
  .colored__line--green {
    background-color: #a9b366;
  }
  .colored__line--yellow {
    background-color: #deba4e;
  }
  .colored__line--red {
    background-color: #ff7e1b;
  }
  .colored__number {
    margin-top: 4px;
    font-size: 11.1px;
    font-weight: bold;
    color: #aaaaaa;
  }
  // table
  .main__table-wrap {
    padding-right: 9%;
    @media only screen and (max-width: 1366px) {
      padding-right: 1%;
    }
    @media only screen and (max-width: 991px) {
      padding-right: 0;
      overflow-x: scroll;
      // width: 992px;
    }
  }
  .main__table {
    width: 100%;
    font-family: $font-primary;
    font-size: 13px;
    font-weight: 500;
    color: #5c5f69;
    @media only screen and (max-width: 991px) {
      width: 1200px;
    }
    thead tr {
      margin: 0;
      font-family: $lato;
      color: #fff;
      font-weight: bold;
      background-color: #71b6cc;
      th {
        height: 41px;
        vertical-align: middle;
      }
    }
    td {
      height: 40px;
      text-align: center;
      vertical-align: middle;
    }
    tbody tr {
      &:nth-child(odd) {
        background-color: #f7f7f7;
      }
      &:nth-child(even) {
        background-color: #f4f4f4;
      }
    }
    .is-checked {
      font-weight: 700;
      .input--checkpseudo {
        .icon {
          visibility: visible;
        }
      }
    }
    // first column begin
    .column-first {
      max-width: 200px;
    }
    .column__first {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 31px;
      padding-right: 12px;
      .icon {
        fill: #fff;
        font-size: 16px;
      }
    }
    .state-wrap {
      display: flex;
      align-items: stretch;
      justify-content: space-between;
      height: 100%;
      padding-right: 10px;
      color: #fff;
      font-weight: bold;
      border-top: 1px solid #fafafa;
    }
    .state-wrap--green {
      background-color: #e4e7d1;
      .state__name {
        background-color: #d3d8b1;
        &:after {
          border-color: transparent transparent transparent #d3d8b1;
        }
      }
      .state__digit {
        background-color: #a9b366;
      }
    }
    .state-wrap--yellow {
      background-color: #f2e5c1;
      .state__name {
        background-color: #ebd594;
        &:after {
          border-color: transparent transparent transparent #ebd594;
        }
      }
      .state__digit {
        background-color: #deba4e;
      }
    }
    .state-wrap--red {
      background-color: #fad6c9;
      .state__name {
        background-color: #fab9a3;
        &:after {
          border-color: transparent transparent transparent #fab9a3;
        }
      }
      .state__digit {
        background-color: #ff7e1b;
      }
    }
    .state__name {
      position: relative;
      flex-basis: 60%;
      display: flex;
      align-items: center;
      padding-left: 15px;
      margin-right: 20px;
      @media only screen and (max-width: 1200px) {
        padding-left: 10px;
        flex-basis: 70%;
      }
      &:after {
        position: absolute;
        top: 0;
        right: -20px;
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 19.5px 0 19.5px 20px;
      }
    }
    .state__digit-wrap {
      display: flex;
      align-items: center;
    }
    .state__digit {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 21px;
      height: 21px;
      font-size: 12.9px;
      border-radius: 50%;
    }
    // first column end
    // checkbox input with pseudo-check
    .input-box {
      position: relative;
      width: 15px;
      height: 14px;
      padding-left: 15px;
      padding-right: 15px;
      cursor: pointer;
    }
    .input--check {
      position: absolute;
      left: 8px;
      top: 2px;
      visibility: hidden;
      cursor: pointer;
      &:checked+span>.icon {
        visibility: visible;
      }
    }
    .input--checkpseudo {
      position: absolute;
      left: 8px;
      top: 2px;
      width: 15px;
      height: 14px;
      background-color: #ffffff;
      border: solid 1px #cccccc;
      .icon {
        visibility: hidden;
        position: absolute;
        left: 1px;
        top: 1px;
        fill: #666666;
        font-size: 11px;
      }
    }
    // end of checkbox button 
    .product__ceil {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    .icon__product {
      display: inline-block;
      width: 40px;
    }
    .icon-roof {
      width: 19px;
      height: 11px;
    }
    .icon-platen {
      width: 18px;
      height: 18px;
    }
    .icon-shower {
      width: 13px;
      height: 16px;
    }
    .icon-house {
      width: 18px;
      height: 15px;
    }
    .icon-wave {
      width: 19px;
      height: 14px;
    }
    .icon-doors {
      width: 15px;
      height: 18px;
    }
  }
  .column-action {
    font-size: 11px;
    width: 63px;
    background-color: #49a2be;
    @media only screen and (max-width: 1366px) {
      width: 35px;
    }
  }
  .icon-action {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    &:hover {
      .icon {
        fill: #ffffff;
      }
    }
    .icon {
      font-size: 18px;
    }
    .icon-tick {
      font-size: 22px;
    }
  }
  .icon__close {
    &:hover {
      background-color: #5c5f69;
    }
  }
  .icon__phone {
    &:hover {
      background-color: #b6be7c;
    }
  }
  .icon__plane {
    &:hover {
      background-color: #f7835b;
    }
  }
  .icon__tick {
    &:hover {
      background-color: #e4c870;
    }
  }
  // table buttons
  .table__buttons-box {
    margin-top: 17px;
    padding-top: 12px;
    display: flex;
    align-items: center;
    border-top: 1px solid #eeeeee;
    .icon-plus {
      font-size: 17px;
      fill: #999933;
    }
    .icon-trash {
      font-size: 17px;
    }
    .icon-arrow-left,
    .icon-arrow-right {
      font-size: 15px;
    }
  }
  .table__button {
    padding-left: 10px;
    padding-right: 10px;
    border-right: 1px solid #ddd;
  }
  .table__button--left {
    padding-right: 3px;
    border-right: none;
  }
  .content__footer {
    padding-left: 48px;
    @media only screen and (max-width: 1200px) {
      padding-left: 2%;
    }
  }
}