.main__table .main__table .state__digit { display: flex; align-items: center; justify-content: center; width: 21px; height: 21px; font-size: 12.9px; border-radius: 50%; }

.main__table .main__table .input-box { position: relative; width: 15px; height: 14px; padding-left: 15px; padding-right: 15px; cursor: pointer; }

.main__table .main__table .input--check { position: absolute; left: 8px; top: 2px; visibility: hidden; cursor: pointer; }

.main__table .main__table .input--check:checked + span > .icon { visibility: visible; }

.main__table .main__table .input--checkpseudo { position: absolute; left: 8px; top: 2px; width: 15px; height: 14px; background-color: #ffffff; border: solid 1px #cccccc; }

.main__table .main__table .input--checkpseudo .icon { visibility: hidden; position: absolute; left: 1px; top: 1px; fill: #666666; font-size: 11px; }

.main__table .main__table .product__ceil { display: flex; justify-content: flex-start; align-items: center; }

.main__table .main__table .icon__product { display: inline-block; width: 40px; }

.main__table .main__table .icon-roof { width: 19px; height: 11px; }

.main__table .main__table .icon-platen { width: 18px; height: 18px; }

.main__table .main__table .icon-shower { width: 13px; height: 16px; }

.main__table .main__table .icon-house { width: 18px; height: 15px; }

.main__table .main__table .icon-wave { width: 19px; height: 14px; }

.main__table .main__table .icon-doors { width: 15px; height: 18px; }

.main__table .column-action { font-size: 11px; width: 63px; background-color: #49a2be; }

@media only screen and (max-width: 1366px) { .main__table .column-action { width: 35px; } }

.main__table .icon-action { display: flex; justify-content: center; align-items: center; height: 100%; }

.main__table .icon-action:hover .icon { fill: #ffffff; }

.main__table .icon-action .icon { font-size: 18px; }

.main__table .icon-action .icon-tick { font-size: 22px; }

.main__table .icon__close:hover { background-color: #5c5f69; }

.main__table .icon__phone:hover { background-color: #b6be7c; }

.main__table .icon__plane:hover { background-color: #f7835b; }

.main__table .icon__tick:hover { background-color: #e4c870; }

.main__table .table__buttons-box { margin-top: 17px; padding-top: 12px; display: flex; align-items: center; border-top: 1px solid #eeeeee; }

.main__table .table__buttons-box .icon-plus { font-size: 17px; fill: #999933; }

.main__table .table__buttons-box .icon-trash { font-size: 17px; }

.main__table .table__buttons-box .icon-arrow-left, .main__table .table__buttons-box .icon-arrow-right { font-size: 15px; }

.main__table .table__button { padding-left: 10px; padding-right: 10px; border-right: 1px solid #ddd; }

.main__table .table__button--left { padding-right: 3px; border-right: none; }

.main__table .content__footer { padding-left: 48px; }

@media only screen and (max-width: 1200px) { .main__table .content__footer { padding-left: 2%; } }
